<!--
 * @Description:位置标点地图（基于百度地图开放平台）
 * @Author:Demon3443002624@qq.com
 * @Date:2022-1-18 15:38:44
 * @LastEditors:Demon3443002624@qq.com
 * @LastEditTime:2022-1-18 15:38:48
-->
<template>
  <div class="baidu-map">
    <div id="baidu-map"></div>
    <el-button
      @click="backHandler"
      title="返回机组当前位置"
      class="back-to-center"
      v-show="backBtnShow"
      icon="el-icon-refresh-left"
    ></el-button>
  </div>
</template>

<script>
import loadBMap from '@/utils/loadBMap'

export default {
  name: `BaiduMap`,
  props: {
    mapData: {
      type: Array,
      default: (_) => []
    }
  },
  data() {
    return {
      backBtnShow: false,
      // 机组状态对应的图标  0 离线 gray 1 停机 white 2 警告 orange 3 报警 red 4 运行 green
      statusColor: ['gray', 'white', 'orange', 'red', 'green']
    }
  },
  watch: {
    mapData(nV, oV) {
      this.renderBMap()
    }
  },
  methods: {
    // 渲染地图
    renderBMap() {
      loadBMap().then((e) => {
        this.backBtnShow = false
        const bMap = e
        const map = new bMap.Map(`baidu-map`, { enableMapClick: false })
        this.bMap = map
        this.mapData.forEach((e) => {
          const point = new bMap.Point(e.Longitude, e.Latitude)
          const opts = {
            width: 200,
            height: 100,
            title: `<div style='font-size:1.2rem;font-weight:600'>${e.DisplayName}</div>`,
            offset: new bMap.Size(0, -10)
          }
          const infoWindow = new bMap.InfoWindow(
            `<el-card class="box-card">

                    <div  class="text item">
                        经度：${e.Longitude}
                    </div>
                    <div  class="text item">
                        纬度：${e.Latitude}
                    </div>
                    <div  class="text item">
                        ${e.InstallationSite}
                    </div>
                  </el-card>
                  `,
            opts
          )
          const icon = new bMap.Icon(
            require(`@/assets/img/mapIcons/ic_gensets_${this.statusColor[e.Status]}.png`),
            new bMap.Size(26, 34)
          )
          const marker = new bMap.Marker(point, { icon })

          marker.addEventListener(`click`, (_) => {
            map.openInfoWindow(infoWindow, point)
          })
          map.addOverlay(marker)
        })
        const pointCenter = new bMap.Point(111, 38)
        this.pointCenter = pointCenter
        map.enableScrollWheelZoom()
        map.centerAndZoom(pointCenter, 5)
        map.addEventListener(`movestart`, (_) => {
          this.backBtnShow = true
        })
        map.addEventListener(`zoomstart`, (_) => {
          this.backBtnShow = true
        })
      })
    },
    // 返回当前位置
    backHandler() {
      // 有动画的移动
      this.bMap.closeInfoWindow()
      //   this.bMap.panTo(this.pointCenter)
      this.bMap.centerAndZoom(this.pointCenter, 5)
      this.backBtnShow = false
    }
  },
  mounted() {
    this.renderBMap()
  }
}
</script>

<style lang='less' scoped>
.baidu-map {
  height: 100%;
  width: 100%;
  #baidu-map {
    height: 100%;
    width: 100%;
  }
  // 返回当前机组定位按钮
  .back-to-center {
    width: 80px;
    position: absolute;
    right: 10px;
    top: 10px;
    z-index: 2;
    background: transparent;
    border: 0;
    font-size: 2.3rem;
  }
}
</style>



