<!--
 * @Description:设备-机组管理-机组分布（架构模式和地图模式）
 * @Author:Demon3443002624@qq.com
 * @Date:2022-1-18 15:00:09
 * @LastEditors: linxu
 * @LastEditTime: 2022-02-14 16:47:56
-->
<template>
  <div>
    <BMainTopComponent />
    <div
      id="crew-distributed"
      v-loading="loading"
      :element-loading-text="loadingMsg"
      element-loading-background="rgba(0, 0, 0, 0.5)"
    >
      <!-- 1.模式选择及组合筛选条件 -->
      <el-row class="toolbar" type="flex" align="middle">
        <el-col :span="5" :offset="1">
          <el-button :active="type === 'map' ? true : false" @click="type = 'map'" size="medium"
            >地图模式</el-button
          >
          <el-button :active="type === 'tree' ? true : false" @click="type = 'tree'" size="medium"
            >架构模式</el-button
          >
        </el-col>
        <el-col :span="9">
          <el-row type="flex" v-if="type === 'map'" justify="center" align="middle">
            <el-col :span="12">
              <el-row type="flex" align="middle" justify="center">
                <label>机组名称：</label>
                <el-input v-model="mchNameMap" placeholder="请输入机组名称"></el-input>
              </el-row>
            </el-col>
            <el-col :span="12" class="fix-left-margin">
              <el-row type="flex" align="middle" justify="center">
                <label>&nbsp;&nbsp;机组状态：</label>
                <el-select v-model="mchState" placeholder="请选择机组状态">
                  <el-option
                    v-for="item in mchStateOpt"
                    :key="item.value"
                    :label="item.label"
                    :value="item.value"
                  >
                  </el-option>
                </el-select>
              </el-row>
            </el-col>
          </el-row>
          <el-row type="flex" v-if="type === 'tree'" justify="center" align="middle">
            <el-col :span="12">
              <el-row type="flex" align="middle" justify="center">
                <label>机组名称：</label>
                <el-input v-model="mchNameTree" placeholder="请输入机组名称"></el-input>
              </el-row>
            </el-col>
            <el-col :span="12">
              <el-row type="flex" align="middle" justify="center">
                <label>&nbsp;&nbsp;公司名称：</label>
                <el-input v-model="companyName" placeholder="请输入公司名称"></el-input>
              </el-row>
            </el-col>
          </el-row>
        </el-col>
        <el-col :span="9" :pull="1">
          <el-row type="flex" justify="end">
            <el-button size="medium" type="primary" @click="_query">快速查询</el-button>
            <el-button size="medium" type="text" @click="_clear">重置筛选条件</el-button>
          </el-row>
        </el-col>
      </el-row>
      <!-- 2.地图和架构的盒子 -->
      <el-row class="box">
        <!-- 2.1 架构模式 -->
        <el-row class="tree" v-show="type === 'tree'">
          <mch-tree :json="treeData" />
        </el-row>
        <!-- 2.2 地图模式 -->
        <el-row class="map" v-show="type === 'map'">
          <baidu-map :mapData="mapData" />
        </el-row>
      </el-row>
    </div>
  </div>
</template>
<script>
import MchTree from '@/components/MchTree'
import BaiduMap from '@/components/BaiduMap'

export default {
  name: `CrewDistribute`,
  components: {
    MchTree,
    BaiduMap
  },
  data() {
    return {
      // 地图/架构模式
      type: 'map',
      // 加载遮罩
      loading: false,
      loadingMsg: '数据加载中，请稍后',
      // 筛选条件状态
      mchNameMap: '',
      mchNameTree: '',
      mchState: 4,
      companyName: '',
      // 机组状态选项（可能从服务器获取）
      mchStateOpt: [],
      // 地图数据（机组位置信息）
      mapData: [],
      treeData: {}
    }
  },
  methods: {
    // 获取机组状态选项数据
    getMchStateOpt() {
      const res = [
        {
          value: 0,
          label: `全部`
        },
        {
          value: 1,
          label: `在线`
        },
        {
          value: 2,
          label: `报警`
        },
        {
          value: 3,
          label: `警告`
        },
        {
          value: 4,
          label: `运行`
        },
        {
          value: 5,
          label: `停机`
        },
        {
          value: 6,
          label: `离线`
        }
      ]
      this.mchStateOpt = res
    },
    // 根据机组名称和机组状态查询机组位置数据
    getMapData() {
      this.$api.common
        .findAllGeneratorSetsForMap({
          model: {
            StatusMode: this.mchState,
            MoreName: this.mchNameMap
          }
        })
        .then((res) => {
          if (this.type === 'map' && (!res || !res.Data || res.Data.length < 1)) {
            this.$message('当前条件下无机组数据！')
          }
          const Data = res.Data
          this.mapData = Data
        })
    },
    // 根据公司名称（模糊）和机组名称（模糊）查询公司-机组架构图数据
    getTreeData() {
      this.type === 'tree' && (this.loading = true)
      this.$api.common
        .getCustomersSimpleGensetV2Tree({
          model: {
            Customer: this.companyName,
            MoreName: this.mchNameTree,
            IsShowAll: true,
            IsShowSelf: true
          }
        })
        .then((res) => {
          this.type === 'tree' && (this.loading = false)
          if (this.type === 'tree' && (!res || !res.Data || res.Data.children.length < 1)) {
            this.$message('当前条件下无机组数据！')
          }
          const Data = res.Data
          const data = Data.children
          const treeData = {
            id: 0,
            name: Data.CompanyName,
            level: 0,
            children: []
          }
          data.forEach((e, index) => {
            const aCompany = {
              id: index,
              name: e.CompanyName,
              level: 1,
              data: e.children[0].Childrens
            }
            treeData.children.push(aCompany)
          })
          this.treeData = treeData
        })
        .catch((err) => {
          console.error(`ERROR in '@/pages/views/crew-distribute/index.vue'`, err)
        })
    },
    // 查询按钮
    _query() {
      this.type === 'map' && this.getMapData()
      this.type === 'tree' && this.getTreeData()
    },
    // 清除筛选条件按钮
    _clear() {
      if (this.type === 'map') {
        this.mchNameMap = ''
        this.mchState = 4
        this.getMapData()
      }
      if (this.type === 'tree') {
        this.mchNameTree = ''
        this.companyName = ''
        this.getTreeData()
      }
      this.$message({
        message: `筛选条件已重置！`,
        type: `success`
      })
    }
  },
  mounted() {
    this.getMchStateOpt()
    this.getMapData()
    this.getTreeData()
  }
}
</script>
<style lang="less" scoped>
// 地图和架构盒子的高度
@height-box: calc(100% - 50px);
// [x] 修复qq浏览器flex布局问题
#crew-distributed {
  // display: block;
  display: flex;
}
// label 标签显示手型
label {
  display: flex;
  cursor: pointer;
  white-space: nowrap;
  align-items: center;
  justify-content: center;
}
// input 按钮使用矩形边框，选中时替换样式
.el-button {
  border-radius: 0;
  &[active] {
    background: #1abc9c;
    color: white;
  }
}
// 1.工具栏样式
.toolbar {
  height: 48px;
  background: #f3f3f3;
  border: 1px solid #e4e4e4;
  // elementUI select 和 input 渲染宽度不一致导致切换地图/架构模式之后出现布局不协调，此处处理一下
  .fix-left-margin {
    display: flex;
  }
  .el-button[active] {
    background: #1f927b !important;
    color: white !important;
  }
}
// 2.地图/架构盒子
.box {
  margin: 10px 0;
  height: @height-box;
  // border: 1px solid rgb(228, 214, 214);
  > div {
    height: 100%;
  }
}
</style>



